<template>
  <div class="three" >
    <template v-if="rsvshow">
      <p v-for="text in texts" :key="text">
        {{text}}
      </p>
      <template v-if="arr.length === 0">
        <button class="rsv" @click="resolve" :style="{top: position.top, left: position.left}">‚ù§Ô∏è ÂêåÊÑèÂú®‰∏ÄËµ∑</button>
        <button v-if="rjtshow" class="rjt" @click="rjt" :style="{top: '400px'}">‚ùå ‰Ω†ËÉñÊàëÁò¶Êàë‰ª¨‰∏çÊòØ‰∏ÄÁ±ª‰∫∫</button>
      </template>
    </template>
    <template v-else>
      <p>Âæê‰∫∫Êù∞üíóÈ≤çÈõ™Ëéπ</p>
      <p>Â∑≤ÁªèÂú®‰∏ÄËµ∑‰∫Ü</p>
      <p>‚ù§Ô∏è‚ù§Ô∏è‚ù§Ô∏è‚ù§Ô∏è‚ù§Ô∏è‚ù§Ô∏èüíó‚ù§Ô∏è‚ù§Ô∏è‚ù§Ô∏è‚ù§Ô∏è‚ù§Ô∏è‚ù§Ô∏è</p>
      <p>{{countdownDetail.y}}Âπ¥</p>
      <p>{{countdownDetail.m}}Êúà</p>
      <p>{{countdownDetail.d}}Êó•</p>
      <p>{{countdownDetail.h}}Êó∂</p>
      <p>{{countdownDetail.min}}ÂàÜ</p>
      <p>{{countdownDetail.s}}Áßí</p>
    </template>

    
  </div>
</template>

<script>

export default {
  mounted() {
    setInterval(() => {
      const item = this.arr.shift();
      if(item)this.texts.push(item);
    }, 10)
  },
  data() {
    const date = new Date()
    return {
      rjtshow: true,
      rsvshow: true,
      countdown: 0,
      arr: [
        'ÊâÄ‰ª•ÔºåÁé∞Âú®Âæê‰∫∫Êù∞Ê≠£ÂºèÂú∞Ë∑ü‰Ω†Ë°®ÁôΩ',
        'üë®',
        '‰ªñÂæàÂñúÊ¨¢‰Ω†',
        'üåπ',
        `‰ªäÂ§©ÊòØ${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`,
        'üìÖ',
        'ËøôÂ∞Ü‰ºöÊòØÂÄºÂæóÁ∫™ÂøµÁöÑ‰∏ÄÂ§©',
        'üëß',
        'È≤çÈõ™ËéπÂ∞èÁõÜÂèãÔºå‰Ω†Ë¶Å‰∏çË¶ÅÂíåÂæê‰∫∫Êù∞Âú®‰∏ÄËµ∑Ôºü',
        '‚≠ïÔ∏è',
        'ËØ∑ÈÄâÊã©'
      ],
      texts: [],
      rjts: [],
      rsv: [{
        top: '300px',
        left: '100px'
      }, {
        top: '400px',
        left: '200px'
      }, {
        top: '100px',
        left: '100px'
      }, {
        top: '300px',
        left: '200px'
      } ,{
        top: '150px',
        left: '80px'
      } ,{
        top: '60px',
        left: '180px'
      }, {
        top: '200px',
        left: '40px'
      }, {
        top: '300px',
        left: '100px'
      }, {
        top: '400px',
        left: '200px'
      }, {
        top: '100px',
        left: '100px'
      }, {
        top: '300px',
        left: '200px'
      } ,{
        top: '150px',
        left: '80px'
      } ,{
        top: '60px',
        left: '180px'
      }, {
        top: '200px',
        left: '40px'
      },  {
        top: '400px',
        left: '40%'
      }],
      position: {
        top: '400px',
        left: '15%'
      }
    }
  },
  methods: {
    rjt() {
      alert('‰Ω†ËøôÂè™Â∞èÁå™Áå™ÂæàÊ£íÊ£íÂïäÔΩû')
      alert('Â±ÖÁÑ∂Êï¢ÊåâÊãíÁªùËøô‰∏™ÊåâÈíÆ')
      alert('ÈÉΩËØ¥‰∫ÜÊàë‰ºöÊää‰Ω†ÂñÇÊàêüê∑')
      alert('ÊâÄ‰ª•ÂÜçÁªô‰Ω†‰∏ÄÊ¨°Êú∫‰ºö')
      alert('ËÄå‰∏îÊàëÊòéÊòé‰∏çËÉñÔºåÊâÄ‰ª•Ê≤°ÊúâÊãíÁªùËøô‰∏™ÊåâÈíÆ')
      this.rjtshow = false
      this.position = {
        top: '400px',
        left: '40%'
      }
    },
    resolve() {
      const item = this.rsv.shift()
      if(!item) {
        let date = Date.now().valueOf()
        alert(`Â•Ω‰∫ÜÔºåÁúã‰Ω†ËøΩÊàëËøô‰πà‰πÖÔºåÊàëÂêåÊÑèÂï¶ÔºåÂú®‰∏ÄËµ∑ÔºåÁé∞Âú®ÁöÑÊó∂Èó¥Êà≥ÊòØ${date}, Êà™ÂõæËÆ∞‰ΩèËøô‰∏™Êó∂Èó¥Êà≥`)
        this.rsvshow = false
        this.date = date
        setInterval(() => {
          this.countdown = Date.now().valueOf() - this.date
        }, 1000)
        
      } else {
        this.position = item
        if(this.rsv.length === 14  ) {
          alert('ÂñúÊ¨¢ÊàëÂ∞±Êù•ËøΩÊàëÂëÄÔΩû')
        }
        if(this.rsv.length === 8  ) {
          alert('ÊàëÂèØÊ≤°ÈÇ£‰πàÂ•ΩËøΩÔΩû')
        }
        if(this.rsv.length === 2 ) {
          alert('‰Ω†ËÖøÁúüÈïøÔºåÂ∞±Âø´ËøΩ‰∏ä‰∫ÜÔºåÂä†Ê≤πÔºÅ')
        }
        if(this.rsv.length === 1) {
          this.rjtshow = false
        }
      }
      
    }
  },
  computed: {
    countdownDetail() {
      let ct = this.countdown
      let y = 0,m = 0,d = 0,h = 0,min = 0,s = 0
      if(ct/1000/60/60/24/30/12 > 1) {
        y = Math.floor(ct/1000/60/60/24/30/12)
        ct = ct - (y * 1000 * 60 * 60 * 24 *30 * 12)
      }
      if(ct/1000/60/60/24/30 > 1) {
        m = Math.floor(ct/1000/60/60/24/30)
        ct = ct - (m * 1000 * 60 * 60 * 24 *30)
      }
      if(ct/1000/60/60/24 > 1) {
        d = Math.floor(ct/1000/60/60/24)
        ct = ct - (d * 1000 * 60 * 60 * 24)
      }
      if(ct/1000/60/60 > 1) {
        h = Math.floor(ct/1000/60/60)
        ct = ct - (h * 1000 * 60 * 60)
      }
      if(ct/1000/60 > 1) {
        min = Math.floor(ct/1000/60)
        ct = ct - (min * 1000 * 60)
      }
      if(ct/1000 > 1) {
        s = Math.floor(ct/1000)
        ct = ct - (s * 1000)
      }
      return { y,m,d,h,min,s }
    }
  }
}
</script>

<style>
@keyframes dance {
    0% {
        transform: scale(1.0);
    }
    100% {
        transform: scale(2.0);
    }
}
.three {
  background-color: #f9f1ff;
  height: 100%;
  margin-top: -18px;
  position: relative;
}
.three p:nth-child(1) {
  padding-top: 120px;
}

.three p {
  margin-top: 20px;
}

.rsv {
  transition: all .2s;
  -moz-transition: all .2s; /* Firefox 4 */
  -webkit-transition: all .2s; /* Safari Âíå Chrome */
  padding: 10px 20px;
  border: 1px solid #d2d2d2;
}

.rjt {
  padding: 10px 20px;
  border: 1px solid #d2d2d2;
  top: 800px;
  left: 400px;
}

.three button {
  position: absolute;
  font-size: 20px;
  background-color: #fff;
}

</style>



